import Nav from './components/Nav';
import Carrres from './components/Carrres';
import { Query } from 'react-apollo';
import DATA_QUERY from './components/dataquery';
import { BrowserRouter as Router, Routes, Route} from 'react-router-dom'

function App() {
  
    return (
      <Query query={DATA_QUERY}>
       {({ loading, error, data }) => {
        
          if (loading) return <div>Fetching Data.....</div>
          if (error)   return <div>Error Fetching Data</div>

          var items;

          for(let i in data.assets){
            if(data.assets[i].id === "clo5gs4xr160q0bo8uzki9pb6"){
              items = data.assets[i];
            }
          }

          // console.log(items)
          return (
            
              <div className="App">
             <Router>
            <Nav/>
            <Routes>
              <Route extact path="/" element={<Carrres key={items.id} product={items} />} />
              {/* <Route path="/about" element={<About />} />
              <Route path="/services" element={<Services />} /> */}

           </Routes>
    </Router>

               </div>
              
            
          )
        }}
      </Query>
    )
  
}

export default App;
